.mobile-overlay {
    width: 100%;
    height: 100%;
    border: none;
    margin: 0;

    &:modal {
        max-width: 100%;
        max-height: 100%;
    }

    &__close-button-wrapper {
        display: flex;
        justify-content: end;
    }

    &__list {
        @include flex-center;

        flex-direction: column;
        row-gap: 24px;
        min-height: 85vw;
    }
}